
@import "../../base/variables/spacing";
@import "./variable";

@import "../../theme/mixin";
@import "../../base/mixins/border-radius";
@import "../../shadow/mixin";
@import "../../animation/mixin";
@import "./mixin";

// èªéåºå¾ç
.img-fluid {
  @include img-fluid;
}

// Image thumbnails ç¼©ç¥å¾
.img-thumbnail {
  padding: $thumbnail-padding;
  border: $thumbnail-border-width solid $thumbnail-border-color;

  @include theme-prop(background-color, primary-on-dark);

  @include border-radius($thumbnail-border-radius);
  @include animation-transition(all, 200ms, ease-in-out);
  @include shadow(4);

  // ç¡®ä¿ 100%å®½åº¦
  @include img-fluid;
}

//
// Figures
// äººç©
//

.figure {
  display: inline-block;
}

.figure-img {
  margin-bottom: ($spacer / 2);
  line-height: 1;
}

.figure-caption {
  font-size: $figure-caption-font-size;
  @include theme-prop(background-color, secondary-on-dark);
  text-align: center;
}

// åè§å¾ç
.img-rounded {
  border-radius: $thumbnail-border-radius;
}

// åå½¢å¾ç
.img-circle {
  border-radius: 50%;
}



