@import "../../theme/mixin";

// ç±ä¸é¨åç»æï¼slider å®¹å¨ï¼æ»åè¿åº¦ï¼è½¨è¿¹ï¼ï¼æ»åæé®
.slider {
  position:                    relative;
  width:                       100%;
  height:                      48px;
  //ç¨æ¥åªåè®¸æ°´å¹³æ¹åæ»å¨ https://developer.mozilla.org/zh-CN/docs/Web/CSS/touch-action
  touch-action:                pan-x;
  cursor:                      pointer;
  -webkit-tap-highlight-color: rgba(#000, 0);

  &-track {
    position: absolute;
    top:      50%;
    width:    100%;
    height:   2px;
    overflow: hidden;
    @include theme-prop(background-color, primary-alpha);
  }

  &-track-inner {
    position:         absolute;
    height:           100%;
    transform-origin: left top;
    will-change:      transform;
    @include theme-prop(background-color, primary);
  }
  // æ»åæé®
  &-button {
    position:    absolute;
    top:         15px;
    left:        0;
    width:       20px;
    height:      100%;
    user-select: none;
    will-change: transform;
  }

  &-button-inner {
    position: absolute;
    top:          0;
    left:         0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    @include theme-prop(background-color, primary);
    transform:    scale(.6);
    transition:   transform 100ms ease-out;
  }
}



