@import "../../theme/mixin";
@import "../../responsive/mixin";
@import "../../responsive/variable";
@import "../../animation/function";
@import "../../shadow/mixin";
@import "../../ripple/mixin";
@import "variable";

// å¯¼èªå®¹å¨
.navbar {
  position:        relative;
  box-sizing:      border-box;
  display:         flex;
  flex-wrap:       wrap; // å½å¯¼èªè¿å¤æ¶ï¼åè®¸æè¡å¤ç
  align-items:     center;
  justify-content: space-between;
  padding:         0 $navbar-padding-x $navbar-padding-y;

  // Navbar brand
  // è¯¥æ ·å¼ç¨æ¥è®¾ç½® brand, project, or site çåå­ï¼å¦ä¸ä¸ªå°±æ¯å½è®¾å¤åå°åï¼åªæ¾ç¤ºè¯¥åç§°
  &-brand {
    box-sizing:   border-box;
    display:      inline-flex;
    margin-right: $navbar-brand-margin-right;
    font-size:    $navbar-brand-font-size;
    line-height:  inherit;
    white-space:  nowrap;
    @include theme-prop(color, primary-on-light);

    &:focus,
    &:hover {
      text-decoration: none;
    }
  }

  // å½å°å±å¹æ¶ï¼éèå¯¼èªï¼éè¿è¯¥æé®æ¾ç¤ºå¯¼èª
  &-toggler {
    box-sizing:         content-box;
    display:            inline-flex;
    align-items:        center;
    height:             $navbar-toggler-height;
    padding:            $navbar-item-padding-y;
    border:             0; // remove default button style
    background:         transparent; // remove default button style
    @include theme-prop(color, secondary-on-light);
    cursor:             pointer;
    -webkit-appearance: none;

    &-icon {
      display:    inline-block;
      width:      1.5rem;
      height:     2px;
      box-shadow: 0 -10px, 0 -2px, 0 6px;
    }

    &:hover {
      text-decoration: none;
      @include theme-prop(color, primary-on-light);
    }
  }

  // æ§å¶å¯¼èªæ¾ç¤º
  &-collapse {
    display:     none;
    flex-basis:  100%;
    align-items: center;
    opacity:     0;
    transition:  animation-enter(opacity, 120ms);

    // æ·»å  show æ ·å¼æ¥æ¾ç¤ºå¯¼èªï¼TODO éè¦å®ç°å¨ç»ï¼å¾å¤çï¼å¼å§é«åº¦ä¸º0ï¼åè®¾ç½®é«åº¦ï¼
    &.show {
      display: block;
      opacity: 1;
    }
  }
}

// å½å¨ç§»å¨ç«¯æ¶ï¼å¦æé»è®¤ä¸æ³éè nav å¯ä»¥ä½¿ç¨è¯¥æ ·å¼
@include media-breakpoint-down(xs) {
  .navbar-brand {
    padding-top:    $navbar-item-padding-y;
    padding-bottom: $navbar-item-padding-y;
  }

  .navbar-scroll {
    max-width: 100%;
    overflow:  hidden;

    .navbar-nav {
      overflow-x:                 auto;
      -webkit-overflow-scrolling: touch;
      white-space:                nowrap;
    }
  }
}

// å¯¼èªåè¡¨æ ï¼ä¸ nav-link ä¸èµ·ä½¿ç¨
.navbar-nav {
  display:        flex;
  flex-direction: column;
  padding-left:   0;
  margin-bottom:  0;
  margin-left:    0;
  font-size:      $navbar-font-size;
  list-style:     none;
}

.nav-link {
  box-sizing:    border-box;
  display:       inline-flex;
  padding:       $navbar-item-padding-y $navbar-item-padding-x;
  margin-right:  .25rem;
  border-bottom: $nav-link-border-width solid transparent;
  cursor:        pointer;
  @include theme-prop(color, secondary-on-light);

  &:hover {
    text-decoration: none;
    @include theme-prop(color, primary-on-light);
    @include theme-prop(border-bottom-color, secondary-on-light);
  }

  &.active {
    @include theme-prop(color, primary-on-light);
    @include theme-prop(border-bottom-color, primary-on-light);
  }

  &.disabled {
    border-bottom-color: transparent;
    pointer-events:      none;
    cursor:              default;
    @include theme-prop(color, disabled-on-light);
  }
}

// å¯¼èªå±å¼åï¼ä½¿ç¨ååºå¼å¤ç
// .navbar-expand .navbar-expand-sm .navbar-expand-md .navbar-expand-lg .navbar-expand-xl
// å¦æä½¿ç¨ .navbar-expand åä¸ä¼éçå±å¹åå°èæ¹å
// å»ºè®®ä½¿ç¨ .navbar-expand-smï¼å½å±å¹å°äº 600px æ¶éèå¤ç
.navbar-expand {
  @each $breakpoint in map-keys($responsive-breakpoints) {
    $next: breakpoint-next($breakpoint, $responsive-breakpoints);
    $infix: breakpoint-infix($next, $responsive-breakpoints);

    &#{$infix} {
      @include media-breakpoint-up($next) {
        flex-direction:  row;
        flex-wrap:       nowrap;
        justify-content: flex-start;

        .navbar-nav {
          flex-direction: row;
        }

        .navbar-collapse {
          display: flex;
          opacity: 1;
        }

        .navbar-toggler {
          display: none;
          &.show {
            display: block;
          }
        }
      }
    }
  }
}

// åºå®å¨é¡¶é¨
.navbar-top {
  position: sticky;
  top:      0;
  // todo z-index åºè¯¥è®¾ç½®ä¸ºåéï¼ä¸é¡µé¢ä¸­å¶ä»åç´ çå±çº§å¾å¤ç
  z-index:  100;
  @if ($navbar-shadow-enable) {
    @include shadow(6);
  }
  //box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, .05), inset 0 -1px 0 rgba(0, 0, 0, .1);
}

// å½å¯¼èªè®¾ä¸º fixed æ¶ï¼éè¦ç» navbar ä¸é¢åç´ é¢å¤è®¾ç½® margin-top
.navbar-top-adjust {
  margin-top: $navbar-height;
}

// å®ä¹ä¸»é¢æ ·å¼
@each $style in ("primary", "secondary") {
  $tone: theme-tone($theme-primary);

  .navbar-#{$style} {
    @include theme-prop(background-color, #{$style});

    .navbar-toggler {
      @include theme-prop(color, secondary-on-#{$tone});

      &:hover {
        @include theme-prop(color, primary-on-#{$tone});
      }
    }

    .navbar-brand {
      @include theme-prop(color, primary-on-#{$tone});
    }

    .nav-link {
      @include theme-prop(color, secondary-on-#{$tone});

      &:hover {
        @include theme-prop(color, primary-on-#{$tone});
        @include theme-prop(border-bottom-color, secondary-on-#{$tone});
      }

      &.active {
        @include theme-prop(color, primary-on-#{$tone});
        @include theme-prop(border-bottom-color, primary-on-#{$tone});
      }

      &.disabled {
        @include theme-prop(color, disabled-on-#{$tone});
      }
    }

    // ripple ææ
    .nav-link.ripple {
      &::before,
      &::after {
        // ripple ä¸»é¢æ ·å¼
        @include ripple-theme((theme-style: primary-on-#{$style}, opacity: .16));
      }
    }
  }
}





