// input style
@import "../../base/variables/options";
@import "../../animation/variable";
@import "../../base/variables/form";
@import "../../base/mixins/box-shadow";
@import "../../ripple/mixin";
@import "../../theme/mixin";
@import "../../theme/variable";
@import "../../shadow/mixin";
@import "variable";
@import "function";
@import "mixin";

// å®ä¹å¨ Input åç´ å¤å±åè£¹ä¸
.input {
  position:   relative;
  box-sizing: border-box;
  display:    inline-block;
  width:      $form-input-width;

  // å¦æå å¥ label åè®¾ç½®é´è·
  label > input,
  label + input {
    margin-left: $input-margin-left;
  }
}

// å®ä¹å¨ Input åç´ ä¸
.input-field {
  box-sizing:              border-box;
  width:                   100%;
  padding:                 $input-padding-y $input-padding-x;
  border:                  1px solid $input-border-color;
  line-height:             $input-line-height;
  background:              none;
  background-clip:         padding-box;
  will-change:             opacity, transform, color;
  -webkit-appearance:      none; // å»æ input é»è®¤çè¡ä¸ºåæ ·å¼è¡¨ç°
  -webkit-font-smoothing:  antialiased; // æé¯é½¿ææ
  -moz-osx-font-smoothing: grayscale; // æé¯é½¿ææ

  @if $enable-rounded {
    border-radius: $input-border-radius;
  } @else {
    border-radius: 0;
  }

  // Placeholder
  &::placeholder {
    @include theme-prop(color, hint-on-light);
    opacity:    1;
    transition: input-transition(opacity);
  }

  // å®ä¹ `:focus` æ ·å¼
  &:focus {
    border-color: $grey-color-500;
    outline:      none;
    transition:   input-transition(opacity);

    // è·åç¦ç¹æ¶ï¼éè placeholder
    &::placeholder {
      opacity: 0;
    }
  }

  // Disabled
  &:disabled,
  &[readonly] {
    @include theme-prop(color, disabled-on-light);
    pointer-events: none;
  }
}

// å å¥ .input æ ·å¼åï¼å½è·åç¦ç¹æ¶ï¼placeholder åå®¹éè
// å¦ææ³æ¢å¤ input é»è®¤è¡ä¸ºæ¶ï¼å³å½è·åç¦ç¹æ¶ï¼placeholder åå®¹ä¸éèï¼åªæè¾å¥åå®¹åæéè
// è¿æ¶éè¦å ä¸æ ·å¼ .input-placeholder
.input-placeholder {
  .input-field {
    &::placeholder {
      transition: input-transition(color);
    }
    &:focus {
      &::placeholder {
        opacity: 1;
        @include theme-prop(color, secondary-on-light);
      }
    }
  }
}

// è¡¨åæ ¡éªæ ææ¶
.input-invalid {
  .input-field {
    @include theme-assist(border-color, error);

    // Placeholder
    &::placeholder {
      @include theme-assist(color, error);
    }
  }
}

// æå¹³åè¾å¥æ¡ï¼å³åªæä¸æ¡åºè¾¹æ¡è¡¨å
.input-flat {
  padding-left:  0;
  padding-right: 0;
  border-width:  0 0 1px;
  border-radius: 0;
}

// full width input
.input-full {
  display: block;
  flex:    1;
  width:   100%;

  .input-field {
    display: block;
  }
}

.input-label {
  position:         absolute;
  bottom:           $input-label-bottom;
  left:             0;
  pointer-events:   none;
  cursor:           text;
  transform-origin: left top;
  transition:       input-transition(transform), input-transition(color), input-transition(font-size);
  @include theme-prop(color, hint-on-light);
}

// ç¹å»inputæ¶æç¤ºæå­ç¼©å°ä¸ç§»ï¼å¹¶æ¹åé¢è²ä¸ºé«äº®è²
.input-float-above {
  margin-top: 1.5rem;

  .input-field {
    padding-top:   0;
    padding-left:  0;
    padding-right: 0;
    border-width:  0 0 1px;
    border-radius: 0;
    &::placeholder {
      opacity: 0;
    }
  }

  // å½ input è¾å¥æ¡è·åç¦ç¹æ¶ï¼label åä¸ç§»å¨
  // .input-field:-webkit-autofill + .input-label
  .input-field:focus + .input-label,
  .input-field-focused + .input-label,
  .input-field:-webkit-autofill + .input-label {
    cursor:    auto;
    transform: translateY(-90%) scale(.8, .8);
    @include theme-prop(color, primary-on-light);
  }
}

// è¡¨åæ ¡éªæ ææ¶
.input-invalid {
  .input-label,
  .input-field:focus + .input-label,
  .input-field-focused + .input-label,
  .input-field:-webkit-autofill + .input-label {
    @include theme-assist(color, error);
  }
}

// è¾å¥æ¡å¸¦å¾æ 
.input-icon {
  position: absolute;
  top:      .5rem;
  cursor:   pointer;
}

.input-icon-start {
  .input-field {
    padding-left: 2.5rem;
  }

  .input-icon {
    left:  .5rem;
    right: initial;
  }

  .input-label {
    left: 2.5rem;
  }
}

.input-icon-end {
  .input-field {
    padding-right: 2.5rem;
  }

  .input-icon {
    left:  initial;
    right: .5rem;
  }

  .input-label {
    right: 2.5rem;
  }
}

.input-float-above {
  .input-icon {
    top: -0.5rem;
  }
}

// Sizing
.input-lg {
  @include input-size($input-font-size-lg, $input-padding-x-lg, $input-padding-y-lg, $input-icon-top-lg, 1.25, 0);
}

.input-sm {
  @include input-size($input-font-size-sm, $input-padding-x-sm, $input-padding-y-sm, $input-icon-top-sm, .75, -.5rem);
}

// å®ä¹ä¸»é¢æ ·å¼
@if ($enable-custom-theme and $enable-custom-theme-for-input) {
  // ä¸»é¢è²
  @each $style in ("primary", "secondary") {
    .input-#{$style} {
      .input-field {
        @include theme-prop(border-color, #{$style}-lighter);

        &:focus {
          @include theme-prop(border-color, #{$style});
        }
      }
    }
  }

  // è¾å©é¢è²å¯¹åº input
  @each $theme in $theme-assist {
    $color: map-get($theme-assist-colors, $theme);
    .input-#{$theme} {
      .input-field {
        @include theme-assist(border-color, #{$theme}-light);

        &:focus {
          @include theme-assist(border-color, #{$theme});
        }
      }
    }
  }
}



