@import "../../base/variables/form";
@import "variable";

.form-field {
  display:       flex;
  align-items:   center;
  margin-bottom: $form-field-margin-bottom;

  > label {
    padding-right: $label-spacing;
    order:         0;
  }
}

// è¡å
.form-field-inline {
  display:       inline-flex;
  align-items:   center;
  margin-bottom: $form-field-margin-bottom;

  // é»è®¤è®¾ç½® 80px å³å¯¹é½
  > label {
    width:         $form-label-width;
    padding-right: $label-spacing;
    text-align:    $text-align;
    order:         0;
  }
}

// label å¨å·¦ä¾§æ¾ç¤º
.form-field-left {
  display: inline-flex;

  > label {
    margin-left: auto;
    order:       -1;
  }
}

// label å¨å³ä¾§æ¾ç¤º
.form-field-right {
  display: inline-flex;

  > label {
    margin-right: auto;
    order:        1;
  }
}

// Radio å Checkbox
.radio-checkbox-group {
  display:     inline-flex;
  align-items: center;

  .form-field {
    margin-bottom: 0;
    margin-right:  $radio-checkbox-field-margin-right;

    &:last-child {
      margin-right: 0;
    }
  }
}

// form è¡¨åç»
.form-field-group {
  margin-left:  -$form-field-group-item-margin;
  margin-right: -$form-field-group-item-margin;

  > .form-field {
    display:      inline-flex;
    margin-left:  $form-field-group-item-margin;
    margin-right: $form-field-group-item-margin;
  }
}

// form é¢æ¿
.form-panel {
  padding:       $form-panel-padding;
  border:        1px solid $grey-color-200;
  border-radius: $form-border-radius;
}

// å¯è®¾ç½® label é»è®¤å®½åº¦
.label-inline {
