.menu {
  margin-left: .5rem;
}

.menu-title {
  display: block;
  padding: .3rem .5rem;
  font-weight: 400;
  text-decoration: none;
  @include theme-prop(color, secondary-on-light);

  &:hover, &:focus {
    @include hover-focus {
      text-decoration: none;
      @include theme-prop(color, primary-on-light);
    }
  }

  &.active {
    @include theme-prop(color, primary-on-light);
  }

}

.menu-header {
  @include theme-prop(color, primary-on-light);
}

// ä¸æèåï¼å¦æéç¨ li æ¥ä¹¦åèåï¼åç´æ¥å®ä¹ li æ ·å¼å³å¯
.menu-dropdown {
  @include clearfix();
  //ä¸æèåï¼å­èåé»è®¤æ¯éèç
  &.menu .menu {
    display: none;
  }
  > .menu-item, > li {
    float: left;
    &:hover > .menu {
      display: block;
    }
  }
}

// çº§èèå
.menu-cascaded {
  position: relative;

  > .menu {
    position: absolute;
    top: 0;
    left: 90%;
    width: 100%;
  }

  &:hover > .menu {
    display: block;
  }
}

