@import "./variable";
@import "../../base/variables/zindex";
@import "../../shadow/mixin";
@import "../../theme/mixin";
@import "../../responsive/mixin";
@import "../../animation/function";
@import "./mixin";

.dialog {
  position:        fixed;
  top:             0;
  left:            0;
  z-index:         $zindex-dialog;
  display:         flex;
  align-items:     center;
  justify-content: center;
  width:           100%;
  height:          100%;
  pointer-events:  none;
  visibility:      hidden;

  // é®ç½©å±
  &-backdrop {
    position: fixed;
    top:      0;
    left:     0;
    z-index:  -1;
    width:    100%;
    height:   100%;
    @include theme-prop(background-color, primary-on-light);
    opacity:  0;
  }

  // å¯¹è¯æ¡é¢æ¿
  &-panel {
    display:        inline-flex;
    flex-direction: column;
    width:          calc(100% - #{$dialog-panel-margin-x});
    border-radius:  2px;
    @include theme-prop(background-color, background);
    @include shadow(24);

    // è®¾ä¸º inline-block
    &-inline {
      display:   inline-block;
      max-width: 150px;
    }
  }

  // å¤´é¨
  &-header {
    display:     flex;
    align-items: center;
    padding:     $dialog-header-padding / 2 $dialog-header-padding 0;

    &-empty {
      padding: $dialog-header-padding / 2 0;
    }

    // å¤´é¨åå®¹ææ¬
    &-title {
      flex:                    1;
      padding:                 $dialog-header-padding / 2 0;
      margin:                  0;
      font-size:               $dialog-header-font-size;
      font-weight:             $dialog-header-font-weight;
      line-height:             $dialog-header-line-height;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing:  antialiased;
    }
  }

  // å³é­å¾æ 
  &-close {
    padding:     $dialog-header-padding / 2;
    border:      0;
    background:  transparent;
    cursor:      pointer;
    user-select: none;
    outline:     0;
    -webkit-appearance: none;

    // è·åç¦ç¹æ¶ï¼å»æ outline
    &:focus {
      outline: 0;
    }
  }

  // åå®¹
  &-body {
    padding:                 0 $dialog-body-padding $dialog-body-padding;
    margin-top:              $dialog-body-margin-top;
    font-size:               $dialog-body-font-size;
    font-weight:             $dialog-body-font-weight;
    line-height:             $dialog-body-line-height;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing:  antialiased;
    @include theme-prop(color, secondary-on-light);
  }

  // åå®¹å¤ªå¤æ¶ï¼å¯ä»¥åµå¥è¯¥æ ·å¼ï¼æ»å¨åå®¹
  &-scrollable {
    max-height:                 200px;
    border-top:                 1px solid rgba(0, 0, 0, .1);
    border-bottom:              1px solid rgba(0, 0, 0, .1);
    overflow-x:                 auto;
    overflow-y:                 scroll;
    -webkit-overflow-scrolling: touch;
  }

  // åºé¨åºå
  &-footer {
    padding:    0 $dialog-footer-padding $dialog-footer-padding;
    text-align: right;
  }

  // è¿ç¨å°æé®ä¸ï¼éå¸¸ç¨æ¥è®¾ç½®åºé¨æé®
  &-button {
    margin-left:  0;
    margin-right: 1rem;

    &:last-child {
      margin-left:  0;
      margin-right: 0;
    }
  }

  // å±ç¤º
  &-open {
    pointer-events: all;
    visibility:     visible;

    .dialog-backdrop {
      opacity: .3;
    }
  }

  // ä¸º body è®¾ç½®æ ·å¼
  &-scroll-lock {
    height:   100vh;
    overflow: hidden;
  }

  // æå¨ç»æ¶åå°çæ ·å¼å°è£æ mixin æ¹ä¾¿èªå®ä¹å¨ç»æ¶è°ç¨ï¼å±å¼ç»æä¸º
  //.dialog-panel {
  //  opacity: 0;
  //  transform: translateY(150px) scale(.8);
  //}
  //.dialog-open .dialog-panel {
  //  opacity: 1;
  //  transform: translateY(0) scale(1);
  //}
  @include dialog-animation($animation-start: (opacity: 0, transform: translateY(150px) scale(.8)), $animation-end: (opacity: 1, transform: translateY(0) scale(1)));

  // å¨ç»ææ
  &-animating {
    &.dialog {
      transition: animation-enter(visibility, 120ms);
    }
    .dialog-backdrop {
      transition: animation-enter(opacity, 120ms);
    }

    .dialog-panel {
      transition: animation-enter(opacity, 120ms), animation-enter(transform, 120ms);
    }
  }

  // ä»é¡¶é¨å¼¹åºï¼è¯¥ class æ ·å¼ä½ç¨äº dialog-panel ä¸
  .dialog-popup-top {
    position:  absolute;
    top:       0;
    transform: translateY(-200%);
  }

  &.dialog-open .dialog-popup-top {
    transform: translateY(50%);
  }

  // ä»åºé¨é¨å¼¹åºï¼è¯¥ class æ ·å¼ä½ç¨äº dialog-panel ä¸
  .dialog-popup-bottom {
    position:  absolute;
    bottom:    0;
    transform: translateY(200%);
  }

  &.dialog-open .dialog-popup-bottom {
    transform: translateY(-50%);
  }
}

// ååºå¼å¤ç
@include media-breakpoint-down(sm) {
  .dialog {
    min-width: 280px;

    &-panel {
      min-width: 280px;
    }

    &-body {
      line-height: 24px;
    }

    &-footer {
      padding: 0;
    }
  }
}



